* {
    margin: 0;
    border: 0;
    padding: 0;
    overflow: hidden;
}

::selection {
    background-color: rgba(117, 41, 189, 0.7);
}

body {
    width: 100vw;
    height: 100vh;
}

@media (min-width: 0px) {
    .overlay {
        background: none;
        width: 100vw;
        height: 100vh;
        position: fixed;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

    .popup {
        background: rgba(0, 0, 0, 0.8);
        width: 90vw;
        height: 100vw;
        border-radius: 5px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

    .popup-title {
        color: #fff;
        font-size: 10vw;
        font-family: 'Courier New', Courier, monospace;
        font-weight: 100;
    }

    .popup-description {
        color: #fff;
        font-size: 4vw;
        font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS',
            sans-serif;
        font-weight: 100;
        text-align: center;
        margin-top: 4vw;
    }

    .socials-container {
        background: none;
        width: 100vw;
        height: fit-content;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        margin-top: 10vw;
    }

    .social-card {
        background: rgba(0, 0, 0, 0.9);
        width: 30%;
        height: 4vh;
        border-radius: 5px;
        margin-left: 2vw;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        gap: 10%;
        user-select: none;
        cursor: pointer;
    }

    .social-card-text {
        color: #fff;
        font-size: 2.4vw;
        font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    }

    .social-card-icon {
        height: 100%;
        -webkit-user-drag: none;
        background-attachment: none;
    }
}

